export const Verify = (studentInfomation) => {
  var Airtable = require('airtable');
  var base = new Airtable({ apiKey: 'key3fX4iWEdGotDwW' }).base('appvWyF7JgnpEYfm2');
  let name = studentInfomation.fields.name
  console.log("name: ", name);
  let filterByFormula = "AND(name='".concat(name).concat("')");
  let outputList = base("签到表")
    .select({ filterByFormula, view: "Grid view", fields: ["created", "Student"] })
    .firstPage()
  return outputList
}

