import React, { useState } from 'react';
import { GetStudentId } from './airtable/receive';
import { CreateRecord } from './airtable/create';
import { Verify } from './airtable/verify';
import { TitleWrapper, DivWrapper, InputWrapper } from './inputStyled';

const InputData = () => {
  const [disabled, setDisabled] = useState(false);
  return (
    <div>
      <DivWrapper>
        <TitleWrapper>钢琴公寓签到</TitleWrapper>
        <InputWrapper
          autoFocus
          onKeyPress={e => handleChange(e, setDisabled)}
          disabled={disabled}
        />
      </DivWrapper>
    </div>
  );
};

function handleChange(e, setDisabled) {
  const inputValue = e.target.value;
  console.log('input', e.target.which);
  if (e.which === 13) {
    console.log('扫码机');
    console.log(inputValue);
    setDisabled(true);
    let record = GetStudentId(inputValue);
    record.then(result => {
      if (result[0] != null && result[0].id != null) {
        const studentInformation = result[0];
        let verify = Verify(studentInformation);
        verify.then(result => {
          console.log('verify: ', result);
          if (result.length > 0) {
            const dateAttend = result[result.length - 1].fields.created;
            console.log('latest: ', dateAttend);
            const dt = new Date();
            const thisDate = dt.toISOString().substring(0, 10);
            console.log('this date: ', thisDate);
            if (thisDate === dateAttend) {
              console.log('already attended');
              window.location.reload();
              return;
            }
          }
          let created = CreateRecord(studentInformation.id);
          created.then(result => {
            console.log('sign in, record is: ', result);
            window.location.reload();
          });
        });
      } else {
        alert('没有此ID， 请重新输入');
        window.location.reload();
      }
    });
  }
}

export default InputData;
