export function GetStudentId(inputId) {
  var Airtable = require("airtable");
  var base = new Airtable({ apiKey: "key3fX4iWEdGotDwW" }).base(
    "appvWyF7JgnpEYfm2"
  );
  var filterByFormula = "AND(Id='".concat(inputId).concat("')");

  return base("basic")
    .select({ filterByFormula, view: "RawData" })
    .firstPage();
}
